import React, { useState, useEffect, useCallback } from 'react';
import { Link } from 'react-router-dom';
import Navbar from '../layout/Navbar';
import Footer from '../layout/Footer';
import { Helmet } from 'react-helmet';

// Add IntersectionObserver utility
const useIntersectionObserver = (ref, options) => {
  const [isIntersecting, setIsIntersecting] = useState(false);
  
  useEffect(() => {
    const observer = new IntersectionObserver(([entry]) => {
      setIsIntersecting(entry.isIntersecting);
    }, options);
    
    const currentRef = ref.current; // Store ref.current in a variable for cleanup
    
    if (currentRef) {
      observer.observe(currentRef);
    }
    
    return () => {
      if (currentRef) {
        observer.unobserve(currentRef);
      }
    };
  }, [ref, options]);
  
  return isIntersecting;
};

// Lazy loaded image component
const LazyImage = ({ src, alt, className }) => {
  const [imageSrc, setImageSrc] = useState('data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMzIwIiBoZWlnaHQ9IjMyMCIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj48cmVjdCB3aWR0aD0iMzIwIiBoZWlnaHQ9IjMyMCIgZmlsbD0iI2UyZTJlMiIvPjwvc3ZnPg=='); // Tiny placeholder
  const [isLoaded, setIsLoaded] = useState(false);
  const imageRef = React.useRef(null);
  const isInView = useIntersectionObserver(imageRef, { rootMargin: '200px 0px' });
  
  useEffect(() => {
    if (isInView) {
      const img = new Image();
      img.src = src;
      img.onload = () => {
        setImageSrc(src);
        setIsLoaded(true);
      };
    }
  }, [isInView, src]);
  
  return (
    <img 
      ref={imageRef}
      src={imageSrc} 
      alt={alt}
      className={`${className} ${!isLoaded ? 'opacity-60' : ''} transition-opacity duration-300`}
      loading="lazy"
      width="320"
      height="320"
    />
  );
};

const WebStoriesList = () => {
  const [webStories, setWebStories] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [selectedState, setSelectedState] = useState('');
  const [selectedCity, setSelectedCity] = useState('');
  const [visibleItems, setVisibleItems] = useState(12); // Initial load count

  // Fetch web stories with optimization
  useEffect(() => {
    // Add cache control header to prevent cached responses
    fetch('/php/get_web_stories.php', {
      headers: {
        'Cache-Control': 'no-cache, no-store, must-revalidate',
        'Pragma': 'no-cache',
        'Expires': '0'
      }
    })
      .then(response => {
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        return response.json();
      })
      .then(data => {
        setWebStories(data);
        setLoading(false);
      })
      .catch(error => {
        console.error('Error fetching web stories:', error);
        setError(error.toString());
        setLoading(false);
      });
  }, []);

  // Load more items when user scrolls
  const loadMoreItems = useCallback(() => {
    setVisibleItems(prevCount => prevCount + 12);
  }, []);

  // Handle intersection observer for infinite scroll
  useEffect(() => {
    const handleScroll = () => {
      if (window.innerHeight + document.documentElement.scrollTop >= document.documentElement.offsetHeight - 500 
          && webStories.length > visibleItems && !loading) {
        loadMoreItems();
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [webStories, visibleItems, loading, loadMoreItems]);

  // Filter stories based on selection
  const filteredStories = webStories.filter(story => {
    if (selectedState && selectedState !== '' && story.state !== selectedState) return false;
    if (selectedCity && selectedCity !== '' && story.city !== selectedCity) return false;
    return true;
  });

  // Display only the visible batch of items
  const storiesToShow = filteredStories.slice(0, visibleItems);

  if (loading) {
    return (
      <div className="bg-gray-50 min-h-screen flex flex-col">
        <Navbar
          selectedState={selectedState}
          setSelectedState={setSelectedState}
          setSelectedCity={setSelectedCity}
          selectedCity={selectedCity}
        />
        <div className="container mx-auto p-6 flex-grow flex items-center justify-center">
          <div className="animate-spin rounded-full h-32 w-32 border-t-2 border-b-2 border-blue-500"></div>
        </div>
        <Footer />
      </div>
    );
  }

  if (error) {
    return (
      <div className="bg-gray-50 min-h-screen flex flex-col">
        <Navbar
          selectedState={selectedState}
          setSelectedState={setSelectedState}
          setSelectedCity={setSelectedCity}
          selectedCity={selectedCity}
        />
        <div className="container mx-auto p-6 flex-grow">
          <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded">
            <p>Error loading web stories: {error}</p>
          </div>
        </div>
        <Footer />
      </div>
    );
  }

  return (
    <div className="bg-gray-50 min-h-screen flex flex-col">
      <Helmet>
        <title>Egg Rate Web Stories - View Latest Egg Prices Across India</title>
        <meta name="description" content="Browse visual web stories featuring the latest egg rates in cities across India. Get quick and easy access to current egg prices in an engaging format." />
        <meta name="keywords" content="egg rate web stories, egg price updates, visual egg rates, India egg prices, NECC egg rates stories" />
        <link rel="canonical" href="https://todayeggrates.com/webstories" />
      </Helmet>
      <Navbar
        selectedState={selectedState}
        setSelectedState={setSelectedState}
        setSelectedCity={setSelectedCity}
        selectedCity={selectedCity}
      />
      <div className="container mx-auto p-6 flex-grow">
        <h1 className="text-3xl font-bold text-center mb-8">Egg Rate Web Stories</h1>
        
        {storiesToShow.length > 0 ? (
          <>
            <p className="text-gray-600 text-center mb-10">
              Browse our collection of web stories featuring the latest egg rates across different cities in India. 
              Click on any story to view detailed and up-to-date egg prices in a visually engaging format.
            </p>
            
            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
              {storiesToShow.map((story, index) => (
                <Link 
                  to={`/webstory/${story.slug}`} 
                  key={index}
                  className="bg-white rounded-lg overflow-hidden shadow-lg hover:shadow-xl transition-shadow duration-300"
                  aria-label={`Web story about egg rates in ${story.city}, ${story.state}`}
                >
                  <div className="relative">
                    <LazyImage 
                      src={story.thumbnail} 
                      alt={`Egg Rate in ${story.city}, ${story.state}`} 
                      className="w-full h-48 object-cover"
                    />
                    <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/70 to-transparent p-3">
                      <p className="text-white text-sm font-medium">
                        {story.date}
                      </p>
                    </div>
                  </div>
                  <div className="p-4">
                    <h2 className="font-bold text-xl mb-2">{story.title}</h2>
                    <p className="text-gray-600">{story.date}</p>
                    <p className="text-red-600 font-bold mt-2">â¹{story.rate} per egg</p>
                  </div>
                </Link>
              ))}
            </div>
          </>
        ) : (
          <div className="text-center py-10">
            <p className="text-gray-600">No web stories available at the moment.</p>
          </div>
        )}
      </div>
      <Footer />
    </div>
  );
};

export default WebStoriesList;
